.hero-room-lux {
    background-color: #0e0c07;
    z-index: 1;
  }
  
  .hero-room-lux__con {
    position: relative;
    display: flex;
    flex-direction: column;
    padding-top: 451px;
    padding-bottom: 20px;
  
    @media screen and (min-width: 1024px) {
      padding-top: 465px;
      padding-bottom: 24px;
    }
    @media screen and (min-width: 1280px) {
      padding-top: 537px;
      padding-bottom: 48px;
    }
  }
  
  .hero-room__bg {
    background-color: #0e0c0783;
    z-index: 2;
    width: 100%;
    height: 143px;
    position: absolute;
    background: linear-gradient(to bottom, black, rgba(255, 255, 255, 0));
    @media screen and (min-width: 1024px) {
      height: 116px;
    }
    @media screen and (min-width: 1280px) {
      height: 147px;
    }
  }
  
  // -----------------------------Slides
  
  .hero-room__swiper-button-prev {
    position: absolute;
    left: 13px;
    // z-index: 999;
    @media screen and (min-width: 1023px) {
      left: 47px;
    }
    @media screen and (min-width: 1280px) {
      left: 83px;
    }
    
  }
  
  .hero-room__swiper-button-next {
    position: absolute;
    right: 13px;
    @media screen and (min-width: 1023px) {
      right: 47px;
    }
    @media screen and (min-width: 1280px) {
      right: 83px;
    }
    
    z-index: 3;
  }
  
  .swiper-button-prev:after {
    content: '';
  }
  
  .swiper-button-next:after {
    content: '';
  }
  
  .hero-room__arrow-icon {
    fill: #f3c653;
    height: 100%;
    width: 100%;
    display: block;
    background-color: rgba(0%, 0%, 0%, 65%);
  }
  
  .hero-room__swiper {
    height: 740px;
    width: 100%;
    top: 0;
    right: 0;
    position: absolute;
    @media screen and (min-width: 1024px) {
      height: 720px;
    }
    // box-shadow: 2px 2px 60px 2px rgba(0, 0, 0, 0.25);
  }
  
